import { useState, useEffect } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import { AgGridColumn, AgGridReact } from 'ag-grid-react';
import { generate, getCombinationsCount } from './helper/combinations';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import './App.css';

let socket = new WebSocket("wss://domains.appo.su/ws");
let domainStatuses = [];

function App() {
  const [totalCombinations, setTotalCombination] = useState(0);
  const [rowData, setRowData] = useState([]);
  const [generatedDomains, setGeneratedDomains] = useState([]);
  const [alphabetA, setAlphabetA] = useState('aeiouy');
  const [alphabetB, setAlphabetB] = useState('bcdfghjklmnpqrstvwxz');
  const [pattern, setPattern] = useState('ABABAB.com');
  const [generationInProgress, setGenerationInProgress] = useState(false);
  const [socketConnected, setSocketConnected] = useState(false);
  const [checkingInProgress, setCheckingInProgress] = useState(false);
  const [checkedCount, setcheckedCount] = useState(0);  

  function updateAlphabetA(event) {
    setAlphabetA(event.target.value);
    setTotalCombination(getCombinationsCount(pattern, event.target.value, alphabetB));
  }

  function updateAlphabetB(event) {
    setAlphabetB(event.target.value);
    setTotalCombination(getCombinationsCount(pattern, alphabetA, event.target.value));
  }

  function updatePattern(event) {
    setPattern(event.target.value);
    setTotalCombination(getCombinationsCount(event.target.value, alphabetA, alphabetB));
  }

  function generateDomains() {
    setGenerationInProgress(true);
    setTimeout(() => {
      const domains = generate(pattern, alphabetA, alphabetB);
      setGeneratedDomains(domains);
      buildRowData(domains);
      setGenerationInProgress(false);
    }, 100);    
  }

  function checkAvailability() {
    if (!socketConnected) {
      alert('Not connected to ws');
      return false;
    }
    
    setCheckingInProgress(true);
    setcheckedCount(0);

    checkNextDomain();
  }

  function checkNextDomain() {
    const domain = rowData[checkedCount] && rowData[checkedCount].domain;
    if (domain) {      
      socket.send(domain);
    } else {
      setcheckedCount(checkedCount + 1);
      setTimeout(() => {        
        checkNextDomain();
      }, 1); 
    }
  }

  function buildRowData(domains) {
    setRowData(domains.map(domainName => {return {domain: domainName, status: domainStatuses[domainName] || '...'}}));
  }

  function connectToWs() {
    setTimeout(() => {
      socket = new WebSocket("wss://domains.appo.su/ws");
    }, 500);     
  }

  socket.onopen = (e) => setSocketConnected(true);
    
  socket.onmessage = (event) => {
    const domain = rowData[checkedCount] && rowData[checkedCount].domain;      

    if (domain) {
      domainStatuses[domain] = event.data;
      buildRowData(generatedDomains);
    }            

    if (checkedCount + 1 >= rowData.length) {
      setCheckingInProgress(false);
      return;
    }
    
    setcheckedCount(checkedCount + 1);
    setTimeout(() => {        
      checkNextDomain();
    }, 1);      
  }
  
  socket.onclose = (event) => {
    setSocketConnected(false);
    connectToWs(); 
  }
  
  socket.onerror = (error) => {
    console.error(error);
  }

  useEffect(() => {
    setTotalCombination(getCombinationsCount(pattern || '', alphabetA || '', alphabetB || ''));
  }, []);

  return (
    <div className="App">
      <Row className="mt-4">
        <Col>
          <Form.Group>
            <Form.Label>Alphabet A</Form.Label>
            <Form.Control type="text" value={alphabetA} onChange={updateAlphabetA} disabled={generationInProgress} />
          </Form.Group>
          <Form.Group className="mb-2">
            <Form.Label>Alphabet B</Form.Label>
            <Form.Control type="text" value={alphabetB} onChange={updateAlphabetB} disabled={generationInProgress} />
          </Form.Group>
          <Form.Group className="mb-2">
            <Form.Label>Pattern</Form.Label>
            <Form.Control type="text" value={pattern} onChange={updatePattern} disabled={generationInProgress} />
          </Form.Group>          
        </Col>
        <Col>
          <div className="mt-2">
            <Button onClick={generateDomains} disabled={generationInProgress}>Generate {totalCombinations} domains</Button>
          </div>
          <div className="mt-2">
            <Button onClick={checkAvailability} disabled={generationInProgress}>Check Availability</Button>
          </div>
          {checkingInProgress && (
            <div className="mt-2" >
              Checked {checkedCount} from {rowData.length}
            </div>
          )}
        </Col>
      </Row>
      <div className="ag-theme-alpine mt-4" style={{height: 400, width: "100%"}}>
           <AgGridReact rowData={rowData}>
               <AgGridColumn field="domain" sortable={true} filter={true}></AgGridColumn>
               <AgGridColumn field="status" sortable={true} filter={true}></AgGridColumn>
           </AgGridReact>
       </div>       
    </div>
  );
}

export default App;
