/**
 * @param string pattern 
 * @param string alphabetA 
 * @param string alphabetB 
 * @returns array
 */
export function generate(pattern, alphabetA, alphabetB) {
    const lastCombination = getLastPatternCombination(pattern, alphabetA, alphabetB);
    const combinations = [];
    let currentCombination = null;
    while (currentCombination !== lastCombination) {
        currentCombination = getNextCombination(currentCombination, pattern, alphabetA, alphabetB);
        if (currentCombination === null) break;
        combinations.push(currentCombination);
    }
    return combinations;
}

/**
 * @param string pattern 
 * @param string alphabetA 
 * @param string alphabetB 
 * @returns int
 */
 export function getCombinationsCount(pattern, alphabetA, alphabetB) {
    let count = 1;
    for (let c of pattern) {
        if (c === 'A') {
            count *= alphabetA.length;
        } else if (c === 'B') {
            count *= alphabetB.length;
        }
    }
    return count;
}

/**
 * @param string pattern 
 * @param string alphabetA 
 * @param string alphabetB 
 * @returns string
 */
 export function getLastPatternCombination(pattern, alphabetA, alphabetB) {
    let lastCombination = '';
    for (let c of pattern) {
        if (c === 'A') {
            lastCombination += alphabetA.at(-1);
        } else if (c === 'B') {
            lastCombination += alphabetB.at(-1);
        } else {
            lastCombination += c;
        }
    }
    return lastCombination;
}

/**
 * @param string pattern 
 * @param string alphabetA 
 * @param string alphabetB 
 * @returns string
 */
 export function getFirstPatternCombination(pattern, alphabetA, alphabetB) {
    let lastCombination = '';
    for (let c of pattern) {
        if (c === 'A') {
            lastCombination += alphabetA.at(0);
        } else if (c === 'B') {
            lastCombination += alphabetB.at(0);
        } else {
            lastCombination += c;
        }
    }
    return lastCombination;
}

/**
 * @param string combination
 * @param string pattern 
 * @param string alphabetA 
 * @param string alphabetB 
 * @returns string|null
 */
export function getNextCombination(combination, pattern, alphabetA, alphabetB) {
    if (combination === null) {
        return getFirstPatternCombination(pattern, alphabetA, alphabetB);    
    }
    combination = reverseString(combination);
    pattern = reverseString(pattern);
    let nextCombination = '';
    let nextCharacter = null;
    let gettingNext = true;
    for (let i = 0; i < pattern.length; i++) {
        let patternC = pattern[i];
        let currentCharacter = combination[i];
        if (gettingNext) {
            if (patternC === 'A') {
                nextCharacter = getNextCharachter(currentCharacter, alphabetA);
                if (nextCharacter === null) {
                    currentCharacter = alphabetA.at(0);
                } else {
                    gettingNext = false;
                    currentCharacter = nextCharacter;
                }
            } else if (patternC === 'B') {
                nextCharacter = getNextCharachter(currentCharacter, alphabetB);
                if (nextCharacter === null) {
                    currentCharacter = alphabetB.at(0);
                } else {
                    gettingNext = false;
                    currentCharacter = nextCharacter;
                }
            }
        }
        nextCombination += currentCharacter;        
    }
    if (gettingNext) return null;
    return reverseString(nextCombination);
}

/**
 * @param string character
 * @param string alphabet 
 * @returns string|null
 */
 export function getNextCharachter(character, alphabet) {
    const position = alphabet.indexOf(character);
    if (position === -1 || position > alphabet.length - 2) return null;
    return alphabet.at(position + 1);
}

/**
 * @param string character
 * @returns string
 */
function reverseString(str) {
    return str.split("").reverse().join("");
}